import React from 'react'
import classes from './MyPosts.module.css'
import Post from './Post/Post'
import { Field, reduxForm } from 'redux-form'
import { maxLengthCreator, required } from '../../../utils/validator/validators'
import { Textarea } from '../../common/FormsControls/FormsControls'

const maxLength10 = maxLengthCreator(10)

let AddNewPostForm = (props) => {
  return(
    <form onSubmit={props.handleSubmit}>
      <div>
        <Field name='newPostText' component={Textarea} placeholder='Post message'
          validate={[required, maxLength10]}/>
      </div>
      <div>
          <button>Add post</button>
      </div>
    </form>
  )
}

let AddNewPostFormRedux = reduxForm({form: "ProfileAddNewPostForm"})(AddNewPostForm)

const MyPosts = React.memo(props => {
  let postsElements = props.posts
  .map(post => <Post key = {post.id} likesCount = {post.like} png = {post.png} nickname = {post.nickname} message = {post.message}/>)

  let newPostElement = React.createRef();

  // let onAddPost = () => props.onAddPost()

  let onAddPost = (values) =>{
     props.addPost(values.newPostText)
  }
  

  let onPostChange = () => {props.onPostChange(newPostElement.current.value)}

    return(
         <div className={classes.myName}>
          <h3>My posts</h3>
          <AddNewPostFormRedux onSubmit={onAddPost}/>
            {/* <div>
            <div>
            <textarea onChange={onPostChange} ref={newPostElement} value={props.newPostText}/>
            </div>
            <button onClick={onAddPost}>Add post</button>
            </div> */}
            <div className={classes.posts}>
              {postsElements}
            </div>
          </div>  
    )
})

export default MyPosts