import { stopSubmit } from "redux-form"
import { profileAPI, usersAPI } from "../api/api"

const ADD_POST = 'ADD-POST'
const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT'
const SET_USER_PROFILE = 'SET-USER-PROFILE'
const SET_STATUS = 'SET-STATUS'
const SAVE_PHOTOS_SUCCESS = 'SAVE-PHOTOS-SUCCESS'

let initialState = {
    posts: [
        {id: 0, like:'1', nickname: 'banan4ik', message: 'Hi, how are you?', png: 'data:image/jpeg;base64,'},
        {id: 1, like:'0', nickname: 'abrikosik', message: "I'm first...", png: 'https://hh.ru/employer-logo/2444018.png'},
        {id: 2, like:'12', nickname: 'ananasik', message: 'You are the best', png: 'data:image/jpeg;base64,'}
      ],

      newPostText: '',
      profile: null,
      status: "",
}

function profileReducer(state = initialState, action) {

    switch (action.type){
        case ADD_POST:
            let newPost = {
                id: 5,
                nickname: 'banan4ik',
                png: 'data:image/jpeg;base64,',
                message: action.text,
                like: 0
            }

            return {
                ...state,
                posts: [...state.posts, newPost],
                newPostText: ''
            }
  
        case UPDATE_NEW_POST_TEXT:
            
            return {
                ...state,
                newPostText: action.newText
            }

        case SET_STATUS:
            
            return {
                ...state,
                status: action.status
            }

        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            }
        
        case SAVE_PHOTOS_SUCCESS:
            return {
                ...state,
                profile: {...state.profile, photos: action.photos}
            }
        
        
        default:
            
            return state
        
  
      }
} 

export const addPostActionCreator = (text) => ({ type: ADD_POST, text  })

export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile })

export const setStatus = (status) => ({ type: SET_STATUS, status })

export const savePhotoSuccess = (photos) => ({ type: SAVE_PHOTOS_SUCCESS, photos })

export const getUserProfile = (userId) => async (dispatch) =>{
    const response = await usersAPI.getProfile(userId)

    dispatch(setUserProfile(response.data))
}

export const getStatus = (userId) => async (dispatch) =>{
    const response = await profileAPI.getStatus(userId)

   dispatch(setStatus(response.data))
}

export const updateStatus = (status) => async (dispatch) =>{
    const response =  await profileAPI.updateStatus(status)
    
    if(response.data.resultCode === 0){
        dispatch(setStatus(status))
    }
 }

export const savePhoto = (file) => async (dispatch) =>{
    const response =  await profileAPI.savePhoto(file)
    
    if(response.data.resultCode === 0){
        dispatch(savePhotoSuccess(response.data.data.photos))
    }
 }

export const saveProfile = (profile) => async (dispatch, getState) =>{
    const userId = getState().auth.userId
    const response =  await profileAPI.saveProfile(profile)
    
    if(response.data.resultCode === 0){
        dispatch(getUserProfile(userId))
    }else{
        dispatch(stopSubmit("edit-profile", {_error: response.data.messages[0]}))
        return Promise.reject(response.data.messages[0])
    }
 }

export const updateNewPostActionCreator = (text) => ({type: UPDATE_NEW_POST_TEXT, newText: text })

export default profileReducer